import { Typography } from '@material-ui/core'
import amber from '@material-ui/core/colors/amber'
import green from '@material-ui/core/colors/green'
import Snackbar from '@material-ui/core/es/Snackbar/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import SnackbarContent from '@material-ui/core/SnackbarContent'
import { withStyles } from '@material-ui/core/styles'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import CloseIcon from '@material-ui/icons/Close'
import ErrorIcon from '@material-ui/icons/Error'
import InfoIcon from '@material-ui/icons/Info'
import WarningIcon from '@material-ui/icons/Warning'
import classNames from 'classnames'
import React from 'react'
import { blue, red } from '@material-ui/core/colors'
import Flex from './flex'
import getThemeService from '../services/get-theme-service'
import SiteService from '../services/site-service'

const variantIcon = {
  success: CheckCircleIcon,
  warning: WarningIcon,
  error: ErrorIcon,
  info: InfoIcon,
  siteColor: InfoIcon,
}

const styles = {
  success: {
    backgroundColor: green[600],
  },
  error: {
    backgroundColor: red[600],
  },
  info: {
    backgroundColor: blue[600],
  },
  warning: {
    backgroundColor: amber[700],
  },
  siteColor: {
    backgroundColor: getThemeService(SiteService.getSite()).palette.primary.main,
  },
  icon: {
    fontSize: 20,
  },
  iconVariant: {
    opacity: 0.9,
  },
  message: {
    display: 'flex',
    alignItems: 'center',
    padding: '6px 24px 6px 12px',
    marginRight: 20,
    '& > svg': {
      marginRight: 10,
    },
  },
  customContent: {
    padding: 0,
    display: 'block',
    position: 'relative',
    '& > div:nth-child(2)': {
      position: 'absolute',
      top: 7,
      right: 7,
    },
  },
  whiteSpace: {
    whiteSpace: 'pre-line',
  },
}

function SnackbarsExtended(props) {
  const { classes, className, message, onClose, variant, open, ...other } = props
  const Icon = variantIcon[variant]

  return (
    <Snackbar
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      open={open}
      autoHideDuration={7000}
      onClose={onClose}
    >
      <SnackbarContent
        className={classNames(classes[variant], classes.customContent, className)}
        aria-describedby="snackbar-custom"
        message={
          <Flex direction="column">
            <span id="snackbar-custom" className={classes.message}>
              {open && <Icon className={classNames(classes.icon, classes.iconVariant)} />}
              <Typography color="inherit" inline variant="body2" className={classes.whiteSpace}>
                {message}
              </Typography>
            </span>
            <div className="progress-bar-custom" />
          </Flex>
        }
        action={[
          <IconButton key="close" aria-label="Close" color="inherit" onClick={onClose}>
            <CloseIcon className={classes.icon} />
          </IconButton>,
        ]}
        {...other}
      />
    </Snackbar>
  )
}

export default withStyles(styles)(SnackbarsExtended)
