import * as React from 'react'
import { Link } from 'react-router-dom'
import Typography from '@material-ui/core/Typography/Typography'
import { withStyles, Icon, Tooltip, WithStyles } from '@material-ui/core'
import Flex from '../common/flex'
import Article from '../model/article'
import LoadFolderArticleWithoutDisplay from './load-folder-article-without-display'
import SnackbarContext from '../common/snackbar-context'

type Props = {
  article: Article
} & WithStyles<typeof styles>

const styles = {
  underlineOnHover: {
    '& > h6:hover': {
      textDecoration: 'underline !important',
    },
  },
}

const reducer = (state: number, force?: number) => (force ? force : state - 1)

const DisplayFolderChildren = ({ article, classes }: Props) => {
  const [loadAllArticles, setLoadAllArticles] = React.useState<boolean>(false)
  const [totalChildren, dispatch] = React.useReducer(reducer, article.children.length)

  const snackbarContext = React.useContext(SnackbarContext)

  React.useEffect(() => {
    dispatch(article.children.length)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [article])

  React.useEffect(() => {
    if (article.children.length && totalChildren === 0) {
      setTimeout(() => {
        snackbarContext.displaySnackbar(
          'Les articles ont bien t enregistrs ! Vous pouvez maintenant passer en mode dconnect et les consulter sans problme',
          'success'
        )
      }, 300)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [totalChildren])

  if (!article.children[0]) {
    return null
  }

  const childrenListSorted = article.children.sort((a, b) => {
    return a.order - b.order
  })

  return (
    <div className="folder-children-list">
      <Flex direction="row" alignItems="center" justify="center">
        <Tooltip
          title={
            <Typography color="inherit" variant="caption">
              Cliquez ici pour sauvegarder maintenant tous les articles de ce dossier pour pour y accder ultrieurement
              en mode dconnect
            </Typography>
          }
          placement="top"
        >
          <Icon
            color={!loadAllArticles ? 'secondary' : 'disabled'}
            style={{ cursor: !loadAllArticles ? 'pointer' : undefined, marginRight: 5 }}
            onClick={() => setLoadAllArticles(true)}
          >
            save
          </Icon>
        </Tooltip>
        Ce dossier comprend les articles suivants :
      </Flex>
      {childrenListSorted.map((child, index) => (
        <div key={index} className="folder-name">
          <Link
            color="primary"
            to={`/articles/${child.urlId}`}
            style={{ textDecoration: 'none', textAlign: 'left' }}
            className={classes.underlineOnHover}
          >
            <Typography variant="subtitle1">
              {child.order + 1} - {child.title}
            </Typography>
          </Link>
          {loadAllArticles && <LoadFolderArticleWithoutDisplay articleUrlId={child.urlId} finishedCallback={dispatch} />}
        </div>
      ))}
    </div>
  )
}

export default withStyles(styles)(DisplayFolderChildren)
