import React, { useEffect, useState, useContext } from 'react'
import './details-article.css'
import baseBackendUrl from '../common/base-backend-url'
import DisplayArticleHeader from './display-article-header'
import DisplayArticleMain from './display-article-main'
import DisplaySimilarsList from './display-similars-list'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import { Typography, WithStyles, withStyles } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Icon from '@material-ui/core/Icon'
import getThemeService from '../services/get-theme-service'
import SiteService from '../services/site-service'
import { format } from '../common/format'
import Flex from '../common/flex'
import { Link } from 'react-router-dom'
import IsStorageOutdatedService from '../services/is-storage-outdated-service'
import parse from 'date-fns/parse'
import DisplayArticleToc from './display-article-toc'
import SnackbarContext from '../common/snackbar-context'
import axios from 'axios'
import mediumZoom from 'medium-zoom'
import Article from '../model/article'
import { RouteChildrenProps } from 'react-router'
import { ConnectionContext } from '../common/service-worker-manager'

type Props = {} & RouteChildrenProps<{ urlId: string }> & WithStyles<typeof styles>

const styles = {
  error404: {
    height: 'calc(100vh - 64px - 64px)',
  },
}

const site = SiteService.getSite()
const inlineBackgroundColor = {
  backgroundColor: getThemeService(site).palette.tertiary.main,
}

const DetailsArticle = ({ location, match, classes }: Props) => {
  const [loading, setLoading] = useState<boolean>(true)
  const [article, setArticle] = useState<Article>()

  const snackbarContext = useContext(SnackbarContext)
  const connectionContext = useContext(ConnectionContext)

  useEffect(() => {
    setLoading(true)
    setArticle(undefined)
    getArticle()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname])

  useEffect(() => {
    mediumZoom('[data-zoomable]', { background: '#333' })
  })

  const getArticle = async () => {
    const storage = localStorage.getItem('kljfpngidlds')
    let storageValue = storage ? atob(storage) : ''
    const isOutdated = IsStorageOutdatedService(storageValue)
    if (isOutdated) {
      storageValue = ''
    }
    const viewed = storageValue.includes(match!.params.urlId)

    try {
      const { data } = await axios.get(
        baseBackendUrl.getBaseBackendUrl() + '/articles/' + match!.params.urlId + '?' + baseBackendUrl.getSiteParam()
      )
      if (data) {
        data.creationDate = format(parse(data.creationDate, 'yyyy/MM/dd', new Date()), 'd MMMM yyyy')
        if (data.modificationDate) {
          data.modificationDate = format(parse(data.modificationDate, 'yyyy/MM/dd', new Date()), 'd MMMM yyyy')
        }
        document.title = data.title + ' - Support-Vision'
        const canonical = document.querySelector('link[rel="canonical"]')
        if (canonical !== null) {
          // @ts-ignore
          canonical.href = `https://${site}.support-vision.fr/articles/${data.urlId}`
        }
      }

      setArticle(data)
      setLoading(false)

      if (viewed === false) {
        try {
          await axios.put(
            baseBackendUrl.getBaseBackendUrl() +
              '/articles/' +
              match!.params.urlId +
              '?' +
              baseBackendUrl.getSiteParam()
          )
          setArticle({
            ...data,
            views: data.views + 1,
          })

          localStorage.setItem(
            'kljfpngidlds',
            btoa(
              (storageValue !== '' ? storageValue.substring(0, storageValue.length - 23) : '') +
                data.urlId +
                '-lastUpdate==' +
                format(new Date(), 'dd/MM/yyyy')
            )
          )
        } catch (e) {
          !connectionContext.isOffline &&
            snackbarContext.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
        }
      }
    } catch (error) {
      snackbarContext.displaySnackbar(
        connectionContext.isOffline
          ? "Erreur, cet article n'est pas dans le cache et vous tes actuellement en mode dconnect"
          : 'Une erreur est survenue, veuillez recharger la page'
      )
    }
  }

  if (loading) {
    return (
      <Flex direction="row" justify="center" alignItems="center" style={{ height: 300 }}>
        <CircularProgress color="primary" />
      </Flex>
    )
  }
  if (!article) {
    return (
      <Flex className={classes.error404} justify="center" alignItems="center" direction="column">
        <Typography variant="h2">Erreur, l'article correspondant n'a pas t trouv.</Typography>
        {/* 
        // @ts-ignore */}
        <Button component={Link} to="/" color="secondary">
          <Icon>arrow_back_ios</Icon>Retour
        </Button>
      </Flex>
    )
  }

  return (
    <div className="article-page-wrapper">
      <div className="table-of-content-wrapper">
        <DisplayArticleToc article={article} />
      </div>
      <div className="article-wrapper">
        <div className="text-align-left">
          {/* 
          // @ts-ignore */}
          <Button component={Link} to="/" color="secondary">
            <Icon>arrow_back_ios</Icon>Retour
          </Button>
        </div>
        <div style={inlineBackgroundColor}>
          <DisplayArticleHeader article={article} />
          <DisplayArticleMain article={article} />
        </div>
      </div>
      <div className="similars-list-container">
        <DisplaySimilarsList article={article} />
      </div>
    </div>
  )
}

export default withStyles(styles)(DetailsArticle)
