import React from 'react'

export default class Iframe extends React.Component {
  constructor(props) {
    super(props)
    let variableAttribute = {}

    this.props.children.split(' ').forEach(prop => {
      const [variablePropName, variablePropValue] = prop.split('=')
      variableAttribute = Object.assign(variableAttribute, {
        [variablePropName]: (variablePropValue || '').replace(/"/g, ''),
      })
    })

    this.state = { variableAttribute }
  }

  render() {
    const { variableAttribute } = this.state

    return (
      <span style={{ textAlign: 'center', textIndent: 0, display: 'block' }}>
        <iframe title="video" {...variableAttribute} style={{ width: '100%' }} />
      </span>
    )
  }
}
