import React, { useState, useContext, useEffect } from 'react'
import baseBackendUrl from '../common/base-backend-url'
import Typography from '@material-ui/core/Typography'
import { Fade, withStyles, createStyles, WithStyles, Icon } from '@material-ui/core'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import Flex from '../common/flex'
import DisplayArticleTile from '../common/display-article-tile'
import parse from 'date-fns/parse'
import { format } from '../common/format'
import SnackbarContext from '../common/snackbar-context'
import axios from 'axios'
import Article from '../model/article'
import { ConnectionContext } from '../common/service-worker-manager'
import useWindowResize from '../hooks/useWindowResize'
import useEventListener from '../hooks/useEventListener'

type Props = {
  article: Article
} & WithStyles<typeof styles>

const styles = createStyles({
  typography: {
    marginBottom: 20,
  },
  arrowScrollUp: {
    position: 'absolute',
    top: 60,
    zIndex: 1,
    fontSize: '2.5rem',
    borderRadius: '50%',
    backgroundColor: 'white',
    width: 30,
    height: 30,
    cursor: 'pointer',
  },
  arrowScrollDown: {
    position: 'absolute',
    bottom: 10,
    fontSize: '2.5rem',
    borderRadius: '50%',
    backgroundColor: 'white',
    width: 30,
    height: 30,
    zIndex: 1,
    cursor: 'pointer',
  },
})

const DisplaySimilarsList = ({ article, classes }: Props) => {
  const [similarArticles, setSimilarArticles] = useState<Article[]>()
  const [wrapperScrollStyle, setWrapperScrollStyle] = useState<any>({ top: 'auto' })
  const [showScrollArrowTop, setShowScrollArrowTop] = useState<boolean>(false)
  const [showScrollArrowBottom, setShowScrollArrowBottom] = useState<boolean>(false)
  const [listScrollStyle, setListScrollStyle] = useState<any>({})
  const [windowAbove1400, setWindowAbove1400] = useState<boolean>(window.innerWidth > 1400)

  const snackbarContext = useContext(SnackbarContext)
  const connectionContext = useContext(ConnectionContext)

  useEffect(() => {
    getSimilars()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    if (!!document.getElementById('similars-list') && windowAbove1400) {
      handleListScroll()
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [windowAbove1400])

  const handleWindowResize = () => {
    if (window.innerWidth > 1400 && !windowAbove1400) {
      setWindowAbove1400(true)
    } else if (window.innerWidth <= 1400 && windowAbove1400) {
      setWindowAbove1400(false)
      setWrapperScrollStyle({ top: 'auto' })
      setListScrollStyle({})
      setShowScrollArrowBottom(false)
      setShowScrollArrowTop(false)
    }
  }

  const handleDocumentScroll = () => {
    if (window.scrollY >= 84 && wrapperScrollStyle.top === 'auto') {
      setWrapperScrollStyle({ top: 0, position: 'fixed', width: 'calc(20% - 1rem - 16px)' })
      setListScrollStyle({ maxHeight: 'calc(100vh - 52px - 2rem - 54px)' })
    } else if (window.scrollY < 84 && wrapperScrollStyle.top === 0) {
      setWrapperScrollStyle({ top: 'auto', position: 'initial', width: '100%' })
      setListScrollStyle({})
    }
  }

  const handleListScroll = () => {
    const similarList = document.getElementById('similars-list')
    const newShowScrollArrowBottom = similarList!.scrollHeight - 1 > similarList!.clientHeight + similarList!.scrollTop
    const newShowScrollArrowTop = similarList!.scrollTop > 0

    if (newShowScrollArrowTop !== showScrollArrowTop || newShowScrollArrowBottom !== showScrollArrowBottom) {
      setShowScrollArrowTop(newShowScrollArrowTop)
      setShowScrollArrowBottom(newShowScrollArrowBottom)
    }
  }

  useEventListener(document, 'scroll', handleDocumentScroll, windowAbove1400)
  useEventListener(
    'similars-list',
    'scroll',
    handleListScroll,
    !!document.getElementById('similars-list') && windowAbove1400
  )
  useWindowResize(handleWindowResize)

  const getSimilars = async () => {
    try {
      const { data } = await axios.get<Article[]>(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/similars?articleId=' +
          article.id +
          '&' +
          baseBackendUrl.getSiteParam()
      )

      data.forEach(
        article => (article.creationDate = format(parse(article.creationDate, 'yyyy/MM/dd', new Date()), 'd MMMM yyyy'))
      )

      setSimilarArticles(data)
    } catch (error) {
      snackbarContext.displaySnackbar(
        connectionContext.isOffline
          ? "Erreur la liste des articles similaires n'est pas dans le cache alors que vous tes en mode dconnect"
          : 'Une erreur est survenue, veuillez recharger la page'
      )
    }
  }

  const handleArrowClick = (direction: number) => () => {
    const similarList = document.getElementById('similars-list')
    similarList!.scrollTop = direction === 1 ? 0 : similarList!.scrollHeight
  }

  if (!similarArticles) {
    return (
      <div className="similars-list-wrapper">
        <CircularProgress color="primary" />
      </div>
    )
  }

  if (!similarArticles[0]) {
    return null
  }

  return (
    <div className="similars-list-wrapper" style={wrapperScrollStyle}>
      <Typography className={classes.typography} variant="h2">
        Articles similaires :
      </Typography>
      <div className="similars-list" id="similars-list" style={listScrollStyle}>
        <Fade in={showScrollArrowTop} timeout={300}>
          <Flex className={classes.arrowScrollUp} alignItems="center" justify="center" onClick={handleArrowClick(1)}>
            <Icon>expand_less</Icon>
          </Flex>
        </Fade>
        {similarArticles.map((similarArticle, i) => (
          <DisplayArticleTile key={i} article={similarArticle} isSimilarList={true} windowAbove1400={windowAbove1400} />
        ))}
        <Fade in={showScrollArrowBottom} timeout={300}>
          <Flex className={classes.arrowScrollDown} alignItems="center" justify="center" onClick={handleArrowClick(-1)}>
            <Icon>expand_more</Icon>
          </Flex>
        </Fade>
      </div>
    </div>
  )
}

export default withStyles(styles)(DisplaySimilarsList)
