import * as React from 'react'
import SnackbarsExtended from './snackbar-extended'

type SnackType = 'info' | 'error' | 'success'

const SnackbarContext = React.createContext({
  /* eslint-disable-next-line */
  displaySnackbar: (message: string, type: SnackType = 'error') => {},
})

export class SnackbarContextProvider extends React.Component {
  displaySnackbar = (message: string, type: SnackType = 'error') => {
    this.setState({ open: true, message, type })
  }

  state = {
    open: false,
    type: undefined,
    message: undefined,
  }

  handleSnackbarClose = () => {
    this.setState({ open: false, message: undefined })
  }

  render() {
    const { children } = this.props
    const { open, message, type } = this.state
    return (
      <SnackbarContext.Provider value={{ displaySnackbar: this.displaySnackbar }}>
        <>
          {children}
          <SnackbarsExtended open={open} variant={type} message={message} onClose={this.handleSnackbarClose} />
        </>
      </SnackbarContext.Provider>
    )
  }
}

export default SnackbarContext
