import React from 'react'
import ReactDisqusComments from 'react-disqus-comments'
import { Button } from '@material-ui/core'
import Icon from '@material-ui/core/Icon'
import withStyles from '@material-ui/core/styles/withStyles'
import { isElementInViewport } from '../services/is-element-in-viewport-service'
import axios from 'axios'
import BaseBackendUrl from '../common/base-backend-url'
import { ConnectionContext } from '../common/service-worker-manager'

const style = {
  button: {
    width: '70%',
    marginBottom: '1rem',
  },
  comments: {
    position: 'relative',
    width: '100%',
    marginTop: '2rem',
    '&::before': {
      background: 'linear-gradient(to left, var(--main-color), var(--background-color) 80%)',
      content: '""',
      position: 'absolute',
      borderRadius: 3,
      minWidth: 310,
      width: '100%',
      height: 5,
      top: '-2.4rem',
      left: -5,
    },
  },
}

class DisplayArticleComments extends React.Component {
  constructor(props) {
    super(props)
    this.state = { showDisqus: false }
  }

  static contextType = ConnectionContext

  componentDidMount() {
    !this.context.isOffline && document.addEventListener('scroll', this.handleDocumentScroll)
  }

  componentWillUnmount() {
    document.removeEventListener('scroll', this.handleDocumentScroll)
  }

  handleDocumentScroll = () => {
    isElementInViewport(document.querySelector('#about'), () => {
      this.setState({ showDisqus: true })
      document.removeEventListener('scroll', this.handleDocumentScroll)
      const { article } = this.props
      axios.get(BaseBackendUrl.getBaseBackendUrl() + '/comments/' + article.id)
    })
  }

  handleShowComments = () => {
    this.setState(prevState => ({ showDisqus: !prevState.showDisqus }))
  }

  render() {
    const { article, classes } = this.props
    const { showDisqus } = this.state
    const { isOffline } = this.context

    const url = 'https://www.support-vision.fr/articles/' + article.id

    const identifier =
      article.shareComments && article.parent && article.parent.shareComments ? article.parent.id + '' : article.id + ''

    return (
      <div id="comments-list" className={classes.comments}>
        <Button variant="contained" color="secondary" onClick={this.handleShowComments} className={classes.button}>
          {isOffline ? (
            'Les commentaires ne sont pas disponibles en mode dconnect'
          ) : (
            <>
              {!showDisqus ? 'Afficher' : 'Cacher'} les commentaires
              {!showDisqus ? <Icon>arrow_drop_down</Icon> : <Icon>arrow_drop_up</Icon>}
            </>
          )}
        </Button>
        {showDisqus && (
          <ReactDisqusComments
            shortname="support-vision.disqus.com/embed.js"
            identifier={identifier}
            title={article.title}
            url={url}
          />
        )}
      </div>
    )
  }
}

export default withStyles(style)(DisplayArticleComments)
