import { useEffect, useRef } from 'react'

type HTMLEvent = 'scroll' | 'resize'
/**
 *
 * @param sourceElement DOM element or string id on which you want to listen for @event, use string id when element does not exist on first render
 * @param event String for event's name you want to listen to
 * @param callback Callback function to do things when event is triggered
 * @param shouldListenCond Use for conditional listening in order to removeListener when needed, default to true so it always listens
 */

let element: Document | HTMLElement

function useEventListener(
  sourceElement: Document | HTMLElement | string,
  event: HTMLEvent,
  callback: () => void,
  shouldListenNowCond = true
) {
  const savedCallback: { current: (() => void) | undefined } = useRef()

  // Remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback
  }, [callback])

  // Set up the interval.
  useEffect(() => {
    function onEvent() {
      savedCallback.current!()
    }
    element = sourceElement as Document | HTMLElement
    if (typeof sourceElement === 'string') {
      element = document.getElementById(sourceElement)!
    }
    if (element && shouldListenNowCond) {
      element.addEventListener(event, onEvent)
    }
    return () => element && element.removeEventListener(event, onEvent)
  }, [sourceElement, event, shouldListenNowCond])
}
export default useEventListener
