import React from 'react'
import './app.css'

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles'

import CssBaseline from '@material-ui/core/CssBaseline'
import DisplayAppContent from './common/display-app-content'
import DisplayAppFooter from './common/display-app-footer'
import DisplayAppParent from './common/display-app-parent'
import SiteService from './services/site-service'
import getThemeService from './services/get-theme-service'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import DisplayGDPRBanner from './common/display-gdpr-banner'
import { SnackbarContextProvider } from './common/snackbar-context'
import ServiceWorkerManager from './common/service-worker-manager'

class App extends React.Component {
  constructor(props) {
    super(props)

    this.state = { site: null, theme: null, siteDescription: null }
  }

  componentDidMount() {
    const site = SiteService.getSite()
    const theme = getThemeService(site)

    document.body.style.setProperty('--main-color', theme.palette.primary.main)
    document.body.style.setProperty('--toc-color', theme.palette.toc)
    document.body.style.setProperty('--background-color', theme.palette.tertiary.main)

    this.setState({
      site: site,
      theme: createMuiTheme(theme),
    })
  }

  render() {
    const { theme, site } = this.state

    if (!theme) {
      return <CircularProgress />
    }

    const sitesDescriptions = {
      supra: "Un point de vue sur des faits d'actualit !",
      super: 'De la culture gnrale super claire !',
      supple: "Un coup d'oeil en plus sur de nombreux sujets geek !",
    }

    const logoSrc = site + '.png'

    const inlineStyles = {
      app: {
        backgroundColor: theme.palette.tertiary.main,
        minHeight: '100vh',
        textAlign: 'center',
      },
    }

    return (
      <React.Fragment>
        <SnackbarContextProvider>
          <ServiceWorkerManager>
            <CssBaseline />
            <MuiThemeProvider theme={theme}>
              <div style={inlineStyles.app}>
                {site ? (
                  <DisplayAppContent logoSrc={logoSrc} siteDescription={sitesDescriptions[site]} />
                ) : (
                  <DisplayAppParent sitesDescriptions={sitesDescriptions} />
                )}

                <DisplayAppFooter />
                <DisplayGDPRBanner />
              </div>
            </MuiThemeProvider>
          </ServiceWorkerManager>
        </SnackbarContextProvider>
      </React.Fragment>
    )
  }
}

export default App
