import React from 'react'
import getContentPerTypeService from '../../services/content-converter-service'
import DisplayImageLegend from './display-image-legend'
import CreateAnchorService from '../../services/create-anchor-service'
import classNames from 'classnames'

class DisplayImage extends React.Component {
  constructor(props) {
    super(props)

    /* Pour ajouter de nouveaux lments dans les images, il suffit de les ajouter  la suite de ce tableau
     * puis de crer le composant correspondant dans le render */
    this.typeList = ['src', 'legend']
  }

  render() {
    const { content, className } = this.props
    const contentPerType = getContentPerTypeService(this.typeList, content)

    return (
      <>
        {contentPerType.map((contentPT, index) => {
          return (() => {
            switch (contentPT.type) {
              case this.typeList[0]:
                return (
                  <div key={index} className={classNames('mt-2', className)}>
                    <img
                      src={contentPT.value}
                      alt={contentPT.value}
                      data-zoomable
                      id={CreateAnchorService(contentPerType[1] ? contentPerType[1].value : contentPT.value)}
                    />
                  </div>
                )
              case this.typeList[1]:
                return <DisplayImageLegend key={index} content={contentPT.value} />
              default:
                return null
              // Pas de defaut dans une image
            }
          })()
        })}
      </>
    )
  }
}

export default DisplayImage
