import React from 'react'
import getContentPerTypeService from '../../services/content-converter-service'
import { Link } from 'react-router-dom'
import { withTheme } from '@material-ui/core'
import SiteService from '../../services/site-service'
import BaseBackendUrl from '../../common/base-backend-url'
import Axios from 'axios'

class DisplayLink extends React.Component {
  constructor(props) {
    super(props)

    /* Pour ajouter de nouveaux lments dans les liens, il suffit de les ajouter  la suite de ce tableau
     * puis de crer le composant correspondant dans le render */
    this.typeList = ['href', 'text']
    this.site = SiteService.getSite()
  }

  handleExternalLinkClick = href => () => {
    Axios.get(BaseBackendUrl.getBaseBackendUrl() + '/links/' + href.replace(/\//g, '_'))
  }

  render() {
    const { theme, content } = this.props
    const contentPerType = getContentPerTypeService(this.typeList, content)

    if (!contentPerType[0]) {
      return ''
    }

    const hrefContent = contentPerType.find(contentPerType => {
      return contentPerType.type === this.typeList[0]
    })
    let textContent = contentPerType.find(contentPerType => {
      return contentPerType.type === this.typeList[1]
    })
    let rel = 'noopener noreferrer '
    if (textContent) {
      textContent = textContent.value

      const relStartIndex = textContent.indexOf('[rel]')
      if (relStartIndex !== -1) {
        const relEndIndex = textContent.indexOf('[/rel]')
        rel += textContent
          .slice(relStartIndex + '[rel]'.length, relEndIndex)
          .split(',')
          .join(' ')
        textContent = textContent.slice(0, relStartIndex).trim() + textContent.slice(relEndIndex + '[/rel]'.length)
      }
    }

    const colorLink = {
      color: theme.palette.primary.main,
    }

    const internalLink = hrefContent.value.indexOf(this.site + '.support-vision.fr/articles')

    if (internalLink === -1) {
      return (
        <a
          style={colorLink}
          href={hrefContent.value}
          // eslint-disable-next-line react/jsx-no-target-blank
          target="_blank"
          rel={rel}
          onClick={this.handleExternalLinkClick(hrefContent.value)}
        >
          {textContent}
        </a>
      )
    } else {
      return (
        <Link
          style={colorLink}
          to={
            '/articles/' + hrefContent.value.substring(('https://' + this.site + '.support-vision.fr/articles/').length)
          }
          target="_blank"
          rel={rel}
        >
          {textContent}
        </Link>
      )
    }
  }
}

export default withTheme()(DisplayLink)
