import Typography from '@material-ui/core/Typography/Typography'
import React from 'react'
import Icon from '@material-ui/core/Icon/Icon'
import withStyles from '@material-ui/core/styles/withStyles'

const styles = theme => ({
  articleTitle: {
    fontSize: '3rem',
    [theme.breakpoints.down('xs')]: {
      fontSize: '2rem',
    },
  },
})

class DisplayArticleHeader extends React.Component {
  render() {
    const { article, classes } = this.props

    let readTime = article.readTime
    if (article.children.length) {
      readTime =
        article.children.length +
        ' x ' +
        Math.floor(
          article.children.reduce((acc, c) => {
            return acc + c.readTime
          }, 0) / article.children.length
        )
    }

    return (
      <div className="article-header">
        <Typography variant="h1" color="secondary" className={classes.articleTitle}>
          {article.title}
        </Typography>
        <div className="article-infos">
          <Icon style={{ fontSize: 16, marginBottom: -3 }}>calendar_today</Icon> Parution : {article.creationDate}
          {article.modificationDate ? (
            <span>
              {' '}
              - <Icon style={{ fontSize: 16, marginBottom: -3 }}>update</Icon> Dernire modification :{' '}
              {article.modificationDate}
            </span>
          ) : (
            ''
          )}
          {article.reason ? <span> - Motif : {article.reason}</span> : ''}
          &nbsp;- <Icon style={{ fontSize: 16, marginBottom: -3 }}>visibility</Icon> {article.views}
          &nbsp;- <Icon style={{ fontSize: 16, marginBottom: -3 }}>thumb_up</Icon> {article.upvotes}
          &nbsp;- <Icon style={{ fontSize: 16, marginBottom: -3 }}>thumb_down</Icon> {article.downvotes}
          &nbsp;- <Icon style={{ fontSize: 16, marginBottom: -3 }}>access_time</Icon> {readTime} min
        </div>
      </div>
    )
  }
}

export default withStyles(styles)(DisplayArticleHeader)
