import tocbot from 'tocbot'
import React from 'react'
import Drawer from '@material-ui/core/Drawer'
import Icon from '@material-ui/core/Icon'
import Fab from '@material-ui/core/Fab'
import { withStyles } from '@material-ui/core'

const tocLabelTag = '[tocLabel]'

const styles = theme => ({
  drawer: {
    '&>div:nth-child(2)': {
      maxWidth: 300,
      width: '80%',
      backgroundColor: theme.palette.tertiary.main,
      '&>div': {
        maxHeight: 'calc(100vh - 50px)',
      },
    },
  },
  summary: {
    cursor: 'pointer',
    position: 'relative',
    '& > span': {
      display: 'none',
      position: 'absolute',
      right: 0,
      bottom: 2,
    },
    '&:hover': {
      '& > span': {
        display: 'initial',
      },
    },
  },
})

class DisplayArticleToc extends React.Component {
  state = {
    windowAbove960: window.innerWidth > 960,
    wrapperScrollStyle: {
      width: window.innerWidth < 1400 ? 'calc(20% + 20px)' : 'calc(15% + 20px)',
      left: window.innerWidth < 1400 ? 'calc(-20% - 20px)' : 'calc(-15% - 20px)',
    },
    commentsOffsetTop: 10000,
    drawerOpen: false,
    hasHeadings: true,
  }

  componentDidMount = () => {
    const { windowAbove960 } = this.state

    if (windowAbove960) {
      this.initTocbot()
      document.addEventListener('scroll', this.handleDocumentScroll)
    }
    window.addEventListener('resize', this.handleWindowResize)
  }

  componentWillUnmount = () => {
    const { windowAbove960 } = this.state
    tocbot.destroy()
    if (windowAbove960) {
      document.removeEventListener('scroll', this.handleDocumentScroll)
    }
    window.removeEventListener('resize', this.handleWindowResize)
  }

  componentDidUpdate = (prevProps, prevState) => {
    if (prevProps.article.id !== this.props.article.id) {
      setTimeout(() => {
        /* Refresh tocbot after rendering */
        tocbot.refresh()
      }, 0)
    } else if (
      (!prevState.drawerOpen && this.state.drawerOpen) ||
      (!prevState.windowAbove960 && this.state.windowAbove960)
    ) {
      setTimeout(() => {
        this.initTocbot()
      }, 0)
    } else if (
      (prevState.drawerOpen && !this.state.drawerOpen) ||
      (prevState.windowAbove960 && !this.state.windowAbove960)
    ) {
      tocbot.destroy()
    }
  }

  initTocbot = () => {
    let hasHeadings = false
    tocbot.init({
      tocSelector: '.toc-here',
      contentSelector: '.article-wrapper',
      headingSelector: 'h2, h3, h4',
      ignoreSelector: '.js-toc-ignore',
      headingLabelCallback: heading => {
        hasHeadings = true
        let index = heading.indexOf(tocLabelTag)

        if (index !== -1) {
          const tocData = heading.slice(index + tocLabelTag.length)
          heading = tocData.slice(0, tocData.length - (tocLabelTag.length + 1))
        } else {
          index = heading.indexOf('[tocIgnore]')
          if (index !== -1) {
            const length = '[tocIgnore]'.length
            const indexEnd = heading.indexOf('[/tocIgnore]')
            heading = heading.slice(0, index) + heading.slice(indexEnd + length + 1)
          }
        }

        if (heading.length > 60) {
          const words = heading.substring(0, 60).split(' ')
          words.splice(-1, 1) // remove last word (often truncated)
          heading = words.join(' ') + '...'
        }

        return heading
      },
      onClick: e => {
        e.preventDefault()
        window.history.replaceState({}, e.target.innerHTML, window.location.href.split('#')[0] + e.target.hash)
      },
    })
    if (!hasHeadings) {
      this.setState({ hasHeadings: false })
    } else {
      this.setState({ hasHeadings: true })
    }
  }

  handleWindowResize = () => {
    const { windowAbove960 } = this.state
    if (window.innerWidth > 960 && !windowAbove960) {
      document.addEventListener('scroll', this.handleDocumentScroll)
      this.setState({ windowAbove960: true, drawerOpen: false })
      this.handleDocumentScroll()
    } else if (window.innerWidth <= 960 && windowAbove960) {
      document.removeEventListener('scroll', this.handleDocumentScroll)
      this.setState({
        windowAbove960: false,
        wrapperScrollStyle: { width: 'calc(20% + 20px)', left: 'calc(-20% - 20px)' },
      })
    }
  }

  handleDocumentScroll = () => {
    const { wrapperScrollStyle, commentsOffsetTop } = this.state
    const currentSizes = window.innerWidth < 1400 ? { expct: '20%', not: '15%' } : { expct: '15%', not: '20%' }
    /* Show toc with its width dependant on window width */
    if (
      window.scrollY >= 400 &&
      (wrapperScrollStyle.left !== 0 || wrapperScrollStyle.width.includes(currentSizes.not)) &&
      window.scrollY < commentsOffsetTop
    ) {
      this.setState({
        wrapperScrollStyle: { width: `calc(${currentSizes.expct} + 20px)`, left: 0 },
        commentsOffsetTop: document.querySelector('#comments-list').offsetTop - 700,
      })
      /* Hide toc because scrolling position is at start or at end of the article */
    } else if ((window.scrollY < 400 || window.scrollY > commentsOffsetTop) && wrapperScrollStyle.left === 0) {
      this.setState({ wrapperScrollStyle: { ...wrapperScrollStyle, left: `calc(-${currentSizes.expct} - 20px)` } })
    }
  }

  render = () => {
    const { wrapperScrollStyle, windowAbove960, drawerOpen, hasHeadings } = this.state
    const { classes } = this.props

    if (!hasHeadings) {
      return null
    }

    return (
      <div className="table-of-content-container" style={wrapperScrollStyle}>
        <div className="table-of-content">
          {windowAbove960 ? (
            <>
              <h2 className={classes.summary} onClick={() => document.body.scrollIntoView({ behavior: 'smooth' })}>
                Sommaire : <Icon>arrow_upward</Icon>
              </h2>
              <div className="toc-wrapper">
                <div className="toc-here" />
              </div>
            </>
          ) : (
            <>
              <Fab
                size="medium"
                color="primary"
                style={{ position: 'fixed', right: 20, top: 10, zIndex: 9999, opacity: 0.8 }}
                onClick={() => this.setState(({ drawerOpen }) => ({ drawerOpen: !drawerOpen }))}
              >
                <Icon>list</Icon>
              </Fab>
              <Drawer
                anchor="right"
                open={drawerOpen}
                onClose={() => this.setState({ drawerOpen: false })}
                className={classes.drawer}
              >
                <div className="table-of-content" style={(wrapperScrollStyle, { padding: '0 10px' })}>
                  <h2
                    style={{ textAlign: 'center', position: 'relative' }}
                    onClick={() => document.body.scrollIntoView({ behavior: 'smooth' })}
                  >
                    <Icon style={{ position: 'absolute', bottom: 2, left: 25 }}>arrow_upward</Icon> Sommaire :
                  </h2>
                  <div className="toc-wrapper">
                    <div className="toc-here" />
                  </div>
                </div>
              </Drawer>
            </>
          )}
        </div>
      </div>
    )
  }
}

export default withStyles(styles)(DisplayArticleToc)
