function initSiteName() {
  if (window.location.href.indexOf('supra.support-vision') !== -1) {
    return 'supra'
  }
  if (window.location.href.indexOf('super.support-vision') !== -1) {
    return 'super'
  }
  if (window.location.href.indexOf('supple.support-vision') !== -1) {
    return 'supple'
  }

  return null
}

export default class SiteService {
  static site = initSiteName()

  static getSite() {
    return this.site
  }

  static getSiteRootUrl = () => {
    const port = window.location.href.indexOf('.fr:44380') !== -1 ? '44380' : '443'
    return window.location.href.indexOf('localhost:5000') !== -1
      ? 'http://localhost:5000'
      : `https://${this.site || 'www'}.support-vision.fr:${port}`
  }
}
