import React, { useEffect, useContext, Dispatch, useState } from 'react'
import IsStorageOutdatedService from '../services/is-storage-outdated-service'
import Axios from 'axios'
import BaseBackendUrl from '../common/base-backend-url'
import { format } from 'date-fns'
import SnackbarContext from '../common/snackbar-context'
import Article from '../model/article'
import DisplayArticleComponents from './content/display-article-components';

type Props = {
  articleUrlId: string
  finishedCallback: Dispatch<number | undefined>
}

/* Get article withoug doing anything else (for service worker purposes) - copy of details-article/getArticle without state & display-similars/getSimilars & brothers etc */
const LoadFolderArticleWithoutDisplay = ({ articleUrlId, finishedCallback }: Props) => {
  const [article, setArticle] = useState<Article>()
  const snackbarContext = useContext(SnackbarContext)

  useEffect(() => {
    getArticle()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const getArticle = async () => {
    const storage = localStorage.getItem('kljfpngidlds')
    let storageValue = storage ? atob(storage) : ''
    const isOutdated = IsStorageOutdatedService(storageValue)
    if (isOutdated) {
      storageValue = ''
    }
    const viewed = storageValue.includes(articleUrlId)

    try {
      const { data } = await Axios.get<Article>(
        BaseBackendUrl.getBaseBackendUrl() + '/articles/' + articleUrlId + '?' + BaseBackendUrl.getSiteParam()
      )
      setArticle(data)

      if (viewed === false) {
        try {
          await Axios.put(
            BaseBackendUrl.getBaseBackendUrl() + '/articles/' + articleUrlId + '?' + BaseBackendUrl.getSiteParam()
          )
          
          const storage = localStorage.getItem('kljfpngidlds')
          let storageValue = storage ? atob(storage) : ''

          localStorage.setItem(
            'kljfpngidlds',
            btoa(
              (storageValue !== '' ? storageValue.substring(0, storageValue.length - 23) : '') +
                data.urlId +
                '-lastUpdate==' +
                format(new Date(), 'dd/MM/yyyy')
            )
          )
        } catch (e) {
          snackbarContext.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
        }
      }

      await Promise.all([getSimilars(data), getBrothers(data)])
      setTimeout(() => {
        finishedCallback(undefined)
      }, 300)

    } catch (error) {
      snackbarContext.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
    }
  }

  const getSimilars = async (article: Article) => {
    try {
      return await Axios.get(
        BaseBackendUrl.getBaseBackendUrl() +
          '/articles/similars?articleId=' +
          article.id +
          '&' +
          BaseBackendUrl.getSiteParam()
      )
    } catch (error) {
      return snackbarContext.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
    }
  }

  const getBrothers = async (article: Article) => {
    try {
      return await Axios.get(
        BaseBackendUrl.getBaseBackendUrl() + '/articles/' + article.urlId + '/brothers'
      )
    } catch (error) {
      return snackbarContext.displaySnackbar(
          'Une erreur est survenue, veuillez recharger la page'
      )
    }
  }

  return (
    <div style={{display: 'none'}}>
      {article && <DisplayArticleComponents content={article.content} />}
    </div>
  )
}

export default LoadFolderArticleWithoutDisplay
