import { Link } from 'react-router-dom'
import React from 'react'
import withStyles from '@material-ui/core/styles/withStyles'
import Icon from '@material-ui/core/Icon'
import DisplayParagraph from './display-paragraph'
import { formatThousands } from './formatThousands'
import Flex from './flex'
import Typography from '@material-ui/core/Typography'
import Fab from '@material-ui/core/Fab'
import classNames from 'classnames'

const styles = {
  articleImage: {
    objectFit: 'cover',
    maxWidth: 350,
    maxHeight: 300,
    width: '94vw',
    '@media (max-width: 1015px)': {
      maxWidth: 450,
    },
  },
  articleContent: {
    textAlign: 'left',
    paddingLeft: 20,
    width: 'calc(84vw - 350px - 20px)',
    '@media (max-width: 1400px)': {
      width: 'calc(90vw - 350px - 20px)',
    },
    '@media (max-width: 1015px)': {
      width: 'calc(92vw)',
      paddingLeft: 0,
      paddingTop: 10,
    },
    '@media (max-width: 630px)': {
      width: 'calc(94vw)',
      paddingLeft: 0,
      paddingTop: 10,
    },
  },
  articleTile: {
    padding: '15px 0 15px 0',
    borderTop: '1px solid #ccc',
    wordBreak: 'break-word',
  },
  iconFolder: {
    paddingTop: '4px !important',
  },
  articleIcons: {
    textAlign: 'right',
  },
  articleIcon: {
    marginLeft: 5,
    '&> span': {
      paddingTop: 2,
    },
  },
  articleTags: {
    marginTop: 15,
  },
  articleDescription: {
    marginTop: 15,
  },
  alignRight: {
    textAlign: 'right',
  },
  articleInfos: {
    marginBottom: 10,
    fontSize: '1rem',
  },
  negativeTopMargin: {
    marginTop: -4,
  },
  reducedMaxHeightImage: {
    maxHeight: 200,
  },
}

class DisplayArticleTile extends React.Component {
  render() {
    const { article, classes, isSimilarList, windowAbove1400 } = this.props

    const overrideCssImage = {}
    let overrideCssContent = {}

    let readTime = article.readTime
    if (article.children.length) {
      readTime =
        article.children.length +
        ' x ' +
        Math.floor(
          article.children.reduce((acc, c) => {
            return acc + c.readTime
          }, 0) / article.children.length
        )
    }

    if (isSimilarList && windowAbove1400) {
      overrideCssImage.width = '100%'
      overrideCssImage.paddingLeft = 0
      overrideCssContent = overrideCssImage
      overrideCssContent.paddingTop = 10
    }

    let description = article.description
    if (isSimilarList && windowAbove1400) {
      const tmp = article.description.substr(0, 200).split(' ')
      tmp.splice(-1, 1)
      description = tmp.join(' ') + '...'
    }

    return (
      <div className={classes.articleTile}>
        <Flex direction="row" wrap justify="center" alignItems="center">
          <img
            src={article.descriptionImage}
            style={overrideCssImage}
            className={classNames(
              classes.articleImage,
              isSimilarList && windowAbove1400 && classes.reducedMaxHeightImage
            )}
            alt={article.title}
          />

          <div className={classes.articleContent} style={overrideCssContent}>
            <Link to={`/articles/${article.urlId}`} className="hide-link">
              <Typography variant="caption" className={classes.articleInfos}>
                <Flex justify="space-between" alignItems="center">
                  {article.creationDate}
                  <span
                    className={classNames(classes.articleIcons, article.children.length && classes.negativeTopMargin)}
                  >
                    <span className={classes.articleIcon}>
                      {article.children.length > 0 && (
                        <Icon className={classes.iconFolder} style={{ fontSize: 20 }}>
                          folder
                        </Icon>
                      )}
                      {article.parent && (
                        <Icon className={classes.iconFolder} style={{ fontSize: 20 }}>
                          folder_open
                        </Icon>
                      )}
                    </span>
                    <span className={classes.articleIcon}>
                      <Icon style={{ fontSize: 16 }}>thumb_up</Icon>
                      <span>{formatThousands(article.upvotes)}</span>
                    </span>
                    <span className={classes.articleIcon}>
                      <Icon style={{ fontSize: 16 }}>visibility</Icon>
                      <span>{formatThousands(article.views)}</span>
                    </span>
                    <span className={classes.articleIcon}>
                      <Icon style={{ fontSize: 16, marginRight: 2, height: '1.03em' }}>access_time</Icon>
                      <span>{readTime} min</span>
                    </span>
                  </span>
                </Flex>
              </Typography>
              <Typography variant="h2" className="link-with-underline" color="primary">
                {article.title}
              </Typography>
              <DisplayParagraph content={description} className={classes.articleDescription} isDescription={true} />
            </Link>
            {!isSimilarList && (
              <Flex justify="space-evenly" className={classes.articleTags}>
                {article.tags
                  .filter(tag => tag.weight === 1)
                  .map((tag, index) => (
                    <Fab
                      key={index}
                      component={Link}
                      to={`/${tag.label}`}
                      variant="extended"
                      color="secondary"
                      size="medium"
                    >
                      {tag.label}
                    </Fab>
                  ))}
              </Flex>
            )}
          </div>
        </Flex>
      </div>
    )
  }
}

export default withStyles(styles)(DisplayArticleTile)
