import React, { useEffect, useState, useContext } from 'react'
import DisplayArticleContent from './display-article-content'
import DisplayArticleFooter from './display-article-footer'
import DisplayArticleComments from './display-article-comments'
import DisplayFolderChildren from './display-folder-children'
import DisplayFolderBrothers from './display-folder-brothers'
import DisplayDonationButton from './display-donation-button'
import DisplayAboutAndSNButtons from './display-about-and-sn-buttons'
import Article from '../model/article'
import Axios from 'axios'
import BaseBackendUrl from '../common/base-backend-url'
import SnackbarContext from '../common/snackbar-context'
import { ConnectionContext } from '../common/service-worker-manager'

type Props = {
  article: Article
}

const DisplayArticleMain = ({ article }: Props) => {
  const [brothersArticle, setBrothersArticle] = useState<[Article | undefined, Article | undefined]>()

  const snackbarContext = useContext(SnackbarContext)
  const connectionContext = useContext(ConnectionContext)

  useEffect(() => {
    setBrothersArticle(undefined)
    !!article.parent && getBrothers()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [article])

  const getBrothers = async () => {
    try {
      const { data } = await Axios.get<Article[]>(
        BaseBackendUrl.getBaseBackendUrl() + '/articles/' + article.urlId + '/brothers'
      )

      const prevArticle = data.find((a: Article) => {
        return a.order === article.order - 1
      })
      const nextArticle = data.find((a: Article) => {
        return a.order === article.order + 1
      })

      setBrothersArticle([prevArticle, nextArticle])
    } catch (error) {
      snackbarContext.displaySnackbar(
        connectionContext.isOffline
          ? "Erreur, ce dossier n'est pas correctement enregistr dans le cache et vous tes actuellement en mode dconnect"
          : 'Une erreur est survenue, veuillez recharger la page'
      )
    }
  }

  return (
    <div className="article">
      <div className="article-content">
        {article.parent && brothersArticle && (
          <DisplayFolderBrothers
            parentArticle={article.parent}
            prevArticle={brothersArticle[0]}
            nextArticle={brothersArticle[1]}
            first
          />
        )}
        <DisplayArticleContent article={article} />
        <DisplayFolderChildren article={article} />
        {article.parent && brothersArticle && (
          <DisplayFolderBrothers
            parentArticle={article.parent}
            prevArticle={brothersArticle[0]}
            nextArticle={brothersArticle[1]}
          />
        )}
      </div>
      <DisplayArticleFooter article={article} />
      <DisplayAboutAndSNButtons />
      <DisplayDonationButton />
      <DisplayArticleComments article={article} />
    </div>
  )
}

export default DisplayArticleMain
