import Icon from '@material-ui/core/Icon/Icon'
import React, { useState, useEffect, useContext } from 'react'
import baseBackendUrl from '../common/base-backend-url'
import Fab from '@material-ui/core/Fab'
import SnackbarContext from '../common/snackbar-context'
import Flex from '../common/flex'
import axios from 'axios'
import Article from '../model/article'
import { ConnectionContext } from '../common/service-worker-manager'

type Props = {
  article: Article
}

const DisplayArticleVotes = ({ article }: Props) => {
  const [alreadyUpvoted, setAlreadyUpvoted] = useState<boolean>(false)
  const [alreadyDownvoted, setAlreadyDownvoted] = useState<boolean>(false)
  const snackbarContext = useContext(SnackbarContext)
  const connectionContext = useContext(ConnectionContext)

  useEffect(() => {
    const cookieVal = atob(localStorage.getItem('dldkljsfpngi') || '')
    const alreadyVoted = cookieVal.indexOf(article.id + '-')

    if (alreadyVoted !== -1) {
      cookieVal.substr(alreadyVoted + (String(article.id) + '-').length, 4) === 'true'
        ? setAlreadyUpvoted(true)
        : setAlreadyDownvoted(true)
    }
  }, [article.id])

  const handleUpvote = async () => {
    const sens = alreadyUpvoted ? -1 : 1

    try {
      axios.put(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/' +
          article.urlId +
          '/votes' +
          '?' +
          baseBackendUrl.getSiteParam() +
          '&dldkljsfpngi=' +
          sens +
          '&dldkljsfpngi2=1'
      )
      setAlreadyUpvoted(alreadyUpvoted => !alreadyUpvoted)

      let cookieVal = atob(localStorage.getItem('dldkljsfpngi') || '')
      const alreadyVoted = cookieVal.indexOf(article.id + '-')

      if (alreadyVoted === -1) {
        cookieVal += article.id + '-true,'
      } else {
        cookieVal = cookieVal.replace(article.id + '-true,', '')
      }

      localStorage.setItem('dldkljsfpngi', btoa(cookieVal))
    } catch (error) {
      snackbarContext.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
    }
  }

  const handleDownvote = async () => {
    const sens = alreadyDownvoted ? -1 : 1

    try {
      await axios.put(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/' +
          article.urlId +
          '/votes' +
          '?' +
          baseBackendUrl.getSiteParam() +
          '&dldkljsfpngi=' +
          sens +
          '&dldkljsfpngi2=-1'
      )
      setAlreadyDownvoted(alreadyDownvoted => !alreadyDownvoted)

      let cookieVal = atob(localStorage.getItem('dldkljsfpngi') || '')
      const alreadyVoted = cookieVal.indexOf(article.id + '-')

      if (alreadyVoted === -1) {
        cookieVal += article.id + '-false,'
      } else {
        cookieVal = cookieVal.replace(article.id + '-false,', '')
      }

      localStorage.setItem('dldkljsfpngi', btoa(cookieVal))
    } catch (error) {
      snackbarContext.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
    }
  }

  return (
    <Flex direction="row" wrap alignItems="center" justify="space-evenly">
      <h3 className="js-toc-ignore" style={{ marginRight: 10 }}>
        {article.children[0] ? <span>Ce dossier vous plat </span> : <span>Cet article vous a plu </span>}?
      </h3>
      <Fab
        color={alreadyUpvoted ? 'primary' : 'secondary'}
        size="medium"
        disabled={alreadyDownvoted || connectionContext.isOffline}
        onClick={handleUpvote}
        style={{ marginRight: 10 }}
      >
        <Icon>thumb_up</Icon>
      </Fab>
      <Fab
        color={alreadyDownvoted ? 'primary' : 'secondary'}
        size="medium"
        disabled={alreadyUpvoted || connectionContext.isOffline}
        onClick={handleDownvote}
      >
        <Icon>thumb_down</Icon>
      </Fab>
    </Flex>
  )
}

export default DisplayArticleVotes
