import React from 'react'
import DisplayParagraph from '../../common/display-paragraph'
import DisplayHeader from './display-header'
import DisplayImage from './display-image'
import getContentPerTypeService from '../../services/content-converter-service'

export default class DisplayArticleComponents extends React.Component {
  constructor(props) {
    super(props)

    /* Pour ajouter de nouveaux lments  la racine de l'article, il suffit de les ajouter  la suite de ce tableau
     * puis de crer le composant correspondant dans le render */
    this.typeList = ['h2', 'h3', 'h4', 'p', 'img']
  }

  render() {
    const { style, content } = this.props

    const contentPerType = getContentPerTypeService(this.typeList, content)
    return (
      <div className="article-content-text" style={style}>
        {contentPerType.map((contentPT, index) => {
          return (() => {
            switch (contentPT.type) {
              case this.typeList[0]:
              case this.typeList[1]:
              case this.typeList[2]:
                return <DisplayHeader content={contentPT.value} key={index} type={contentPT.type} />
              case this.typeList[3]:
                return <DisplayParagraph content={contentPT.value} key={index} />
              case this.typeList[4]:
                return <DisplayImage content={contentPT.value} key={index} />
              default:
                return null
              // Pas de default ici car il ne peut y avoir de texte sans lment  la racine de l'article (c'est soit un titre, une citation, un paragraphe etc)
            }
          })()
        })}
      </div>
    )
  }
}
