import React from 'react'
import Typography from '@material-ui/core/Typography/Typography'
import CreateAnchorService from '../../services/create-anchor-service'
import getContentPerTypeService from '../../services/content-converter-service'
import { withStyles, Icon } from '@material-ui/core'
import SnackbarContext from '../../common/snackbar-context'

const styles = {
  header: {
    '& span:nth-child(1)': {
      position: 'relative',
    },
    '& > span:nth-child(2)': {
      display: 'none',
    },
    '&:hover': {
      cursor: 'pointer',
      '& > span:nth-child(2)': {
        display: 'inline-block',
      },
    },
  },
}

class DisplayHeader extends React.Component {
  constructor(props) {
    super(props)

    /* Pour ajouter de nouveaux lments dans les h2, il suffit de les ajouter  la suite de ce tableau
     * puis de crer le composant correspondant dans le render */
    this.typeList = ['tocLabel']
  }

  static contextType = SnackbarContext

  handleHeaderClick = async e => {
    // copy to clipboard
    const url = window.location.href.split('#')[0] + '#' + e.currentTarget.id

    if (!navigator.clipboard) {
      const textArea = document.createElement('textarea')

      // Hack car cette API c'est quand mme bien  chier (dislay none ne fonctionne pas)
      textArea.style.position = 'fixed'
      textArea.style.top = 0
      textArea.style.left = 0
      textArea.style.width = '2em'
      textArea.style.height = '2em'
      textArea.style.padding = 0
      textArea.style.border = 'none'
      textArea.style.outline = 'none'
      textArea.style.boxShadow = 'none'
      textArea.style.background = 'transparent'
      textArea.value = url
      document.body.appendChild(textArea)
      textArea.focus()
      textArea.select()
      try {
        const successful = document.execCommand('copy')
        successful
          ? this.context.displaySnackbar('Url copie dans le presse papier', 'siteColor')
          : this.context.displaySnackbar('Une erreur est survenue, impossible de copier dans le presse papier')
      } catch (err) {
        this.context.displaySnackbar('Une erreur est survenue, impossible de copier dans le presse papier')
      }
      document.body.removeChild(textArea)
      return
    }
    try {
      await navigator.clipboard.writeText(url)
      this.context.displaySnackbar('Url copie dans le presse papier', 'siteColor')
    } catch (err) {
      this.context.displaySnackbar('Une erreur est survenue, impossible de copier dans le presse papier')
    }
  }

  render() {
    const { content, type, classes } = this.props

    const contentPerType = getContentPerTypeService(this.typeList, content)
    return (
      <Typography
        color="secondary"
        variant={type}
        id={CreateAnchorService(contentPerType[0].value)}
        onClick={this.handleHeaderClick}
        className={classes.header}
      >
        {contentPerType.map((contentPT, index) => {
          return (() => {
            switch (contentPT.type) {
              case this.typeList[0]:
                return (
                  <span key={index} style={{ display: 'none' }}>
                    {`[tocLabel]${contentPT.value}[/tocLabel]`}
                  </span>
                )
              default:
                return <span key={index}>{contentPT.value}</span>
            }
          })()
        })}
        <span style={{ position: 'absolute', marginLeft: '0.4rem', transform: 'rotate(90deg)' }}>
          <span style={{ display: 'none' }}>[tocIgnore]</span>
          <Icon color="secondary">link</Icon>
          <span style={{ display: 'none' }}>[/tocIgnore]</span>
        </span>
      </Typography>
    )
  }
}

export default withStyles(styles)(DisplayHeader)
