import React from 'react'
import DisplayArticleTile from '../common/display-article-tile'
import './filterable-liste-articles.css'
import withStyles from '@material-ui/core/styles/withStyles'

const styles = {
  listeArticles: {
    display: 'flex',
    paddingLeft: '8vw',
    paddingRight: '8vw',
    flexDirection: 'column',
    justifyContent: 'space-evenly',
    alignContent: 'flex-start',
    '@media (max-width: 1400px)': {
      paddingLeft: '5vw',
      paddingRight: '5vw',
    },
    '@media (max-width: 1015px)': {
      paddingLeft: '4vw',
      paddingRight: '4vw',
    },
    '@media (max-width: 630px)': {
      paddingLeft: '3vw',
      paddingRight: '3vw',
    },
  },
}

class ShowArticleList extends React.Component {
  render() {
    const { articleList, classes } = this.props

    if (!articleList) {
      return null
    }

    if (!articleList[0]) {
      return (
        <div className={classes.listeArticles}>
          <h3>Il n'y a pas d'articles qui correspondent  vos critres de recherche</h3>
        </div>
      )
    }

    return (
      <div className={classes.listeArticles}>
        {articleList.map((article, i) => (
          <DisplayArticleTile key={i} article={article} />
        ))}
        <div className="filling-empty-space" />
        <div className="filling-empty-space" />
        <div className="filling-empty-space" />
        <div className="filling-empty-space" />
        <div className="filling-empty-space" />
        <div className="filling-empty-space" />
      </div>
    )
  }
}

export default withStyles(styles)(ShowArticleList)
