import * as React from 'react'
import Link from 'react-router-dom/es/Link'
import classnames from 'classnames'

export default class ConditionalLink extends React.Component {
  render() {
    const { children, to, condition, showLink, style, className } = this.props

    if (condition) {
      return (
        <Link className={classnames(!showLink && 'hide-link', className)} color="primary" to={`${to}`} style={style}>
          {children}
        </Link>
      )
    }
    return <div>{children}</div>
  }
}
