import React, { useState, useContext, useEffect } from 'react'
import ShowArticleList from './show-article-list'
import SearchArticles, { FormState } from './search-articles'
import InfiniteScroll from 'react-infinite-scroller'
import baseBackendUrl from '../common/base-backend-url'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import { format } from '../common/format'
import { Icon, withStyles } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import parse from 'date-fns/parse'
import SnackbarContext from '../common/snackbar-context'
import axios from 'axios'
import { WithStyles } from '@material-ui/core'
import Article from '../model/article'
import { ConnectionContext } from '../common/service-worker-manager'
import { RouteChildrenProps } from 'react-router'
import SiteService from '../services/site-service'

type Props = {} & RouteChildrenProps<{ tag: string }> & WithStyles<typeof styles>

const styles = {
  button: {
    marginTop: '2rem',
    marginBottom: '2rem',
  },
}

const sortConverter = {
  true: 'asc',
  false: 'desc',
}

const FilterableListeArticles = ({ match, classes }: Props) => {
  const [articleList, setArticleList] = useState<Article[]>()
  const [hasMore, setHasMore] = useState<boolean>(true)
  const [paramsUrl, setParamsUrl] = useState<string>()
  const [page, setPage] = useState<number>(0)
  const [askToContinue, setAskToContinue] = useState<boolean>(false)

  const snackbarContext = useContext(SnackbarContext)
  const connectionContext = useContext(ConnectionContext)

  useEffect(() => {
    document.title = 'Support-Vision'
    const canonical = document.querySelector('link[rel="canonical"]')
    if (canonical !== null) {
      // @ts-ignore
      canonical.href = 'https://' + SiteService.getSite() + '.support-vision.fr'
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onSearchChange = (state: FormState) => {
    createUrlFromSearch(state)
    setArticleList(undefined)
    setHasMore(true)
    setPage(0)
  }

  const handleContinue = () => {
    getNewPage()
  }

  const createUrlFromSearch = (state: FormState) => {
    const { selectedTagList, sortDir, sortProp, showFolders, showChildren } = state
    let searchParamsUrl = '?'

    searchParamsUrl += `
      tags=${selectedTagList.join(';')}
      &sortDir=${sortConverter[String(sortDir)]}
      &sortProp=${sortProp}
      &showFolders=${showFolders}
      &showChildren=${showChildren}
    `.replace(/\r?\n|\r|\s/g, '')

    setParamsUrl(searchParamsUrl)
  }

  const getNewPage = async () => {
    if (!paramsUrl) {
      return
    }

    if (page % 5 === 0 && page !== 0 && !askToContinue) {
      // Affichage d'un bouton qui demande si on veut continuer ou non le scroll infini
      setHasMore(false)
      return setAskToContinue(true)
    } else if (askToContinue) {
      setAskToContinue(false)
    }

    let itemsPerPage = 2

    if (window.outerWidth >= 840 && window.outerWidth <= 1250) {
      itemsPerPage = 4
    } else if (window.outerWidth > 1250) {
      itemsPerPage = 6
    }

    try {
      const { data } = await axios.get<{ articles: Article[]; hasMore: boolean }>(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles' +
          paramsUrl +
          '&page=' +
          page +
          '&itemsPerPage=' +
          itemsPerPage +
          '&' +
          baseBackendUrl.getSiteParam()
      )

      data.articles.forEach((article: Article) => {
        article.creationDate = format(parse(article.creationDate, 'yyyy/MM/dd', new Date()), 'd MMMM yyyy')
        if (article.modificationDate) {
          article.modificationDate = format(parse(article.modificationDate, 'yyyy/MM/dd', new Date()), 'd MMMM yyyy')
        }
      })

      setArticleList(articles => (articles ? articles.concat(data.articles) : data.articles))
      setHasMore(data.hasMore)
      setPage(page => page + 1)
    } catch (error) {
      setHasMore(false)
      snackbarContext.displaySnackbar(
        connectionContext.isOffline
          ? "Erreur, soit ces critres de recherche ne sont pas enregistrs dans le cache, soit vous ne pouvez pas voir plus d'articles pour les critres courants car il n'y en a pas d'autres dans le cache, et parce que vous tes en mode dconnect"
          : 'Une erreur est survenue, veuillez recharger la page'
      )
    }
  }

  return (
    <div className="filterable-liste-articles">
      <SearchArticles onFormChange={onSearchChange} selectedTag={match && match.params.tag ? match.params.tag : null} />
      <InfiniteScroll
        pageStart={0}
        loadMore={getNewPage}
        hasMore={hasMore}
        loader={
          <div className="loader" key={0}>
            <CircularProgress color="primary" />
          </div>
        }
      >
        <ShowArticleList articleList={articleList} />
      </InfiniteScroll>
      {askToContinue && (
        <Button color="secondary" variant="text" className={classes.button} onClick={handleContinue}>
          Afficher d'autres articles <Icon>arrow_drop_down</Icon>
        </Button>
      )}
    </div>
  )
}

export default withStyles(styles)(FilterableListeArticles)
