import * as React from 'react'
import { Icon } from '@material-ui/core'
import getThemeService from '../services/get-theme-service'
import SiteService from '../services/site-service'

class LinkContinueDescription extends React.Component {
  render() {
    const site = SiteService.getSite()
    const theme = getThemeService(site)
    const colorLink = {
      color: theme.palette.primary.main,
    }
    return (
      <React.Fragment>
        &nbsp;
        <u style={colorLink}>
          Continuer  lire <Icon style={{ marginBottom: -7 }}>arrow_drop_down</Icon>
        </u>
      </React.Fragment>
    )
  }
}

export default LinkContinueDescription
