import React, { useContext, useEffect, useState, ReactChildren } from 'react'
import registerServiceWorker from '../registerServiceWorker'
import SnackbarContext from './snackbar-context'

type Props = {
  children: ReactChildren
}

export const ConnectionContext = React.createContext({
  isOffline: false,
})

const ServiceWorkerManager = ({ children }: Props) => {
  const snackContext = useContext(SnackbarContext)
  const [isOffline, setOffline] = useState<boolean>(false)

  const callbackSwUpdated = () => {
    snackContext.displaySnackbar('Une mise  jour du site est disponible, veuillez rafrachir la page', 'info')
  }

  const callbackOffline = () => {
    snackContext.displaySnackbar(
      "Navigation en mode dconnect, vous n'aurez accs qu'aux articles mis en cache. \nSi la connexion revient, rafrachissez la page pour revenir en mode connect !",
      'info'
    )
    setOffline(true)
  }

  useEffect(() => {
    registerServiceWorker({ callbackSwUpdated, callbackOffline })
    // eslint-disable-next-line
  }, [])

  return <ConnectionContext.Provider value={{ isOffline }}>{children}</ConnectionContext.Provider>
}

export default ServiceWorkerManager
