import * as React from 'react'
import Typography from '@material-ui/core/Typography/Typography'
import Icon from '@material-ui/core/Icon/Icon'
import ConditionalLink from '../common/conditional-link'
import Flex from '../common/flex'
import { withStyles, WithStyles } from '@material-ui/core'
import Article from '../model/article'
import { Link } from 'react-router-dom'

type Props = {
  parentArticle: Article
  prevArticle?: Article
  nextArticle?: Article
  first?: boolean
} & WithStyles<typeof styles>

const styles = {
  underlineOnHover: {
    '& > div > p:hover, > h6:hover': {
      textDecoration: 'underline !important',
    },
  },
}

const DisplayFolderBrothers = ({ parentArticle, prevArticle, nextArticle, first, classes }: Props) => {
  const rootStyle = first ? { marginTop: '2rem', marginBottom: '4rem' } : undefined

  return (
    <div className="folder-brother-list" style={rootStyle}>
      Cet article est contenu dans un dossier :
      <div className="folder-name">
        <Link
          color="primary"
          to={`/articles/${parentArticle.urlId}`}
          style={{ textDecoration: 'none' }}
          className={classes.underlineOnHover}
        >
          <Typography variant="subtitle1">{parentArticle.title}</Typography>
        </Link>
      </div>
      <div className="brother-list">
        <Flex justify="space-between" alignItems="center">
          <ConditionalLink
            condition={prevArticle}
            style={{ textDecoration: 'none' }}
            showLink={true}
            to={prevArticle ? prevArticle.urlId : null}
            className={classes.underlineOnHover}
          >
            <Flex justify="space-between" alignItems="center" style={{ marginRight: '1rem' }}>
              <Icon>arrow_back</Icon>
              <Typography variant="body1" style={{ textIndent: 'initial', marginTop: 0 }}>
                {prevArticle ? prevArticle.title : "Pas d'article prcdent"}
              </Typography>
            </Flex>
          </ConditionalLink>
          <ConditionalLink
            condition={nextArticle}
            style={{ textDecoration: 'none' }}
            showLink={true}
            to={nextArticle ? nextArticle.urlId : null}
            className={classes.underlineOnHover}
          >
            <Flex justify="space-between" alignItems="center" style={{ marginLeft: '1rem' }}>
              <Typography variant="body1" style={{ textIndent: 'initial', marginTop: 0 }}>
                {nextArticle ? nextArticle.title : "Pas d'article suivant"}
              </Typography>
              <Icon>arrow_forward</Icon>
            </Flex>
          </ConditionalLink>
        </Flex>
      </div>
    </div>
  )
}

export default withStyles(styles)(DisplayFolderBrothers)
