import dateFnsFormat from 'date-fns/format'
import fr from 'date-fns/locale/fr'

export const format = (date, dateFormat) => {
  let d = dateFnsFormat(date, dateFormat, { locale: fr })
  if (dateFormat === 'd MMMM yyyy') {
    const split = d.split(' ')
    split[1] = split[1].charAt(0).toUpperCase() + split[1].slice(1)
    d = split.join(' ')
  }
  return d
}
