import { Link } from 'react-router-dom'
import React from 'react'
import Fab from '@material-ui/core/Fab'
import Flex from '../common/flex'

export default class DisplayArticleTagList extends React.Component {
  render() {
    const article = this.props.article

    return (
      <Flex direction="row" wrap alignItems="center" justify="space-evenly" style={{ marginTop: '1rem' }}>
        <h3 className="js-toc-ignore" style={{ marginRight: 10 }}>
          Mots cls :
        </h3>

        {article.tags.map((tag, i) => (
          <Fab
            component={Link}
            size="medium"
            key={i}
            to={`/${tag.label}`}
            variant="extended"
            color="secondary"
            style={{ marginRight: 10 }}
          >
            {tag.label}
          </Fab>
        ))}
      </Flex>
    )
  }
}
