import React from 'react'
import getContentPerTypeService from '../../services/content-converter-service'
import DisplayLink from './display-link'
import { Typography } from '@material-ui/core'
import withStyles from '@material-ui/core/styles/withStyles'

const styles = {
  caption: {
    fontSize: '1rem',
  },
}

class DisplayImageLegend extends React.Component {
  constructor(props) {
    super(props)

    /* Pour ajouter de nouveaux lments dans les images, il suffit de les ajouter  la suite de ce tableau
     * puis de crer le composant correspondant dans le render */
    this.typeList = ['a', 'strong', 'u', 's']
  }

  render() {
    const { content, classes } = this.props

    const contentPerType = getContentPerTypeService(this.typeList, content)

    return (
      <Typography variant="caption" className={classes.caption}>
        {contentPerType.map((contentPT, index) => {
          return (() => {
            switch (contentPT.type) {
              case this.typeList[0]:
                return <DisplayLink key={index} content={contentPT.value} />
              case this.typeList[1]:
                return <strong key={index}>{contentPT.value}</strong>
              case this.typeList[2]:
                return <u key={index}>{contentPT.value}</u>
              case this.typeList[3]:
                return <s key={index}>{contentPT.value}</s>
              default:
                return <React.Fragment key={index}>{contentPT.value}</React.Fragment>
            }
          })()
        })}
      </Typography>
    )
  }
}

export default withStyles(styles)(DisplayImageLegend)
