export default function getThemeService(site) {
  const superTheme = {
    palette: {
      primary: { main: '#0d47a1' },
      secondary: { main: '#0277bd' },
      tertiary: { main: '#e4f1f9' },
      toc: '#42a5f5',
    },
  }
  const supraTheme = {
    palette: {
      primary: { main: '#e56f07' },
      secondary: { main: '#ffa000' },
      tertiary: { main: '#fbefe3' },
      toc: '#ffa000',
    },
  }
  const suppleTheme = {
    palette: {
      primary: { main: '#512da8' },
      secondary: { main: '#7e57c2' },
      tertiary: { main: '#e8eaf6' },
      toc: '#7e57c2',
    },
  }

  return site === 'supple' ? suppleTheme : site === 'supra' ? supraTheme : superTheme
}
