import React, { useState, useEffect, useContext } from 'react'
import Button from '@material-ui/core/Button'
import Icon from '@material-ui/core/Icon'
import SearchTags from './search-tags'
import baseBackendUrl from '../common/base-backend-url'
import CircularProgress from '@material-ui/core/CircularProgress/CircularProgress'
import FormControlLabel from '@material-ui/core/FormControlLabel/FormControlLabel'
import Switch from '@material-ui/core/Switch/Switch'
import Typography from '@material-ui/core/Typography/Typography'
import SnackbarContext from '../common/snackbar-context'
import axios from 'axios'
import { ValueType } from 'react-select'
import Tag from '../model/tag'
import { ConnectionContext } from '../common/service-worker-manager'

type Props = {
  selectedTag: string | null
  onFormChange: (state: FormState) => void
}

export type FormState = {
  selectedTagList: string[]
  sortDir: boolean
  sortProp: string
  showFolders: boolean
  showChildren: boolean
}

const convertSortDirIcon = {
  true: 'up',
  false: 'down',
}

const SearchArticles = ({ selectedTag, onFormChange }: Props) => {
  const [tagList, setTagList] = useState<string[]>()
  const [selectedTagList, setSelectedTagList] = useState<string[]>(selectedTag ? [selectedTag] : [])
  const [sortDir, setSortDir] = useState<boolean>(false)
  const [sortProp, setSortProp] = useState<string>('creationDate')
  const [showFolders, setShowFolders] = useState<boolean>(true)
  const [showChildren, setShowChildren] = useState<boolean>(false)

  const snackbarContext = useContext(SnackbarContext)
  const connectionContext = useContext(ConnectionContext)

  useEffect(() => {
    getAllTags()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    onFormChange(getFormStateObject())
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedTagList, sortDir, sortProp, showFolders, showFolders, showChildren])

  useEffect(() => {
    if (selectedTag || selectedTagList.length > 0) {
      setSelectedTagList(selectedTag ? [selectedTag] : [])
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedTag])

  const getAllTags = async () => {
    try {
      const { data: tagList } = await axios.get<Tag[]>(
        baseBackendUrl.getBaseBackendUrl() + '/tags?' + baseBackendUrl.getSiteParam()
      )
      setTagList(tagList.map(tl => tl.label))
    } catch (error) {
      snackbarContext.displaySnackbar(
        connectionContext.isOffline
          ? "Erreur, la liste des tags n'est pas enregistre dans le cache et vous tes en mode donnect"
          : 'Une erreur est survenue, veuillez recharger la page'
      )
    }
  }

  const handleButtonClick = (btn: string) => (_: any) => {
    setSortDir((sortDir: boolean) => (sortProp === btn ? !sortDir : true))
    setSortProp(btn)
  }

  const handleCheckboxClick = (chk: string) => (_: any) => {
    if (chk === 'folders') {
      setShowFolders(showFolders => !showFolders)
    } else {
      setShowChildren(showChildren => !showChildren)
    }
  }

  const onSelectedTagListChange = (tagList: ValueType<{ label?: string | null; value?: string | null }>) => {
    // @ts-ignore <-- C'est normal, en fait a renvoie un tableau mais les types sont mal fait lib de merde etc :)
    setSelectedTagList(tagList ? tagList.map(tag => tag.label) : [])
  }

  const getFormStateObject = () => ({
    selectedTagList,
    sortDir,
    sortProp,
    showFolders,
    showChildren,
  })

  if (!tagList) {
    return (
      <div className="search-bar">
        <CircularProgress color="primary" />
      </div>
    )
  }

  return (
    <div className="search-bar">
      <div className="search-tags">
        <SearchTags
          tagList={tagList.map(tl => ({ value: tl, label: tl, key: tl }))}
          onSelectedTagListChange={onSelectedTagListChange}
          selectedTagList={selectedTagList.map(tl => ({ value: tl, label: tl, key: tl }))}
        />
      </div>
      <Button color="primary" variant="text" onClick={handleButtonClick('creationDate')}>
        <Icon color="secondary" style={{ fontSize: 40 }}>
          {'arrow_drop_' + (sortProp === 'creationDate' ? convertSortDirIcon[String(sortDir)] : 'up')}
        </Icon>
        <span className="search-bar-icon">
          <Icon color="primary" className="icon-miniature">
            calendar_today
          </Icon>
          &nbsp;Date
        </span>
      </Button>
      <Button color="primary" variant="text" onClick={handleButtonClick('modificationDate')}>
        <Icon color="secondary" style={{ fontSize: 40 }}>
          {'arrow_drop_' + (sortProp === 'modificationDate' ? convertSortDirIcon[String(sortDir)] : 'up')}
        </Icon>
        <span className="search-bar-icon">
          <Icon color="primary" className="icon-miniature">
            update
          </Icon>
          &nbsp;Mise  jour
        </span>
      </Button>
      <Button color="primary" variant="text" onClick={handleButtonClick('views')}>
        <Icon color="secondary" style={{ fontSize: 40 }}>
          {'arrow_drop_' + (sortProp === 'views' ? convertSortDirIcon[String(sortDir)] : 'up')}
        </Icon>
        <span className="search-bar-icon">
          <Icon color="primary" className="icon-miniature">
            visibility
          </Icon>
          &nbsp;Vues
        </span>
      </Button>
      <Button color="primary" variant="text" onClick={handleButtonClick('upvotes')}>
        <Icon color="secondary" style={{ fontSize: 40 }}>
          {'arrow_drop_' + (sortProp === 'upvotes' ? convertSortDirIcon[String(sortDir)] : 'up')}
        </Icon>
        <span className="search-bar-icon">
          <Icon color="primary" className="icon-miniature">
            thumb_up
          </Icon>
          &nbsp;Votes
        </span>
      </Button>

      <FormControlLabel
        control={<Switch checked={showFolders} color="secondary" onChange={handleCheckboxClick('folders')} />}
        label={
          <span className="search-bar-icon">
            <Icon color="primary" className="icon-miniature">
              folder
            </Icon>
            <Typography color="primary" variant="button">
              &nbsp;Dossiers
            </Typography>
          </span>
        }
      />
      <FormControlLabel
        control={<Switch checked={showChildren} color="secondary" onChange={handleCheckboxClick('children')} />}
        label={
          <span className="search-bar-icon">
            <Icon className="icon-miniature" color="primary">
              folder_open
            </Icon>
            <Typography color="primary" variant="button">
              &nbsp;Articles des dossiers
            </Typography>
          </span>
        }
      />
    </div>
  )
}

export default SearchArticles
